.intro {
  background-color: #1d2631;
  color: $color-white;
  padding-top: 28px;
  padding-bottom: 38px;
}

.intro__link {
  display: block;
  color: $color-white;
  text-decoration: none;
  border-radius: 29px;
  border: 3px solid $color-white;
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  padding: 16px 14px;
  margin-bottom: 32px;
}

.intro__list {
  @include list-style-clear;
  margin-bottom: 30px;
  padding-left: 38px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.intro__item:not(:nth-of-type(3n)) {
  margin-right: 35px;
}

.intro__information {
  margin: 0;
  text-align: center;
  font-weight: 300;
  line-height: 24px;
}
